export default function guess() {
  for(let i=0;i<=10;i++){
    let math = parseInt(Math.random() * 3 + 1)
    let text = prompt(`猜拳、和电脑石头剪刀布,请输入1石头,2剪刀,3布,4退出`)   
    if(!isNaN(parseInt(text))) {
       text = parseInt(text)
    }
    switch (text) {
      case 1:
        if (text < math) {
          if (math == 2) {
            alert('你出的是:石头\n电脑出的是:剪刀\n恭喜你赢了本局')
          } else if (math == 3) {
            alert('你出的是:石头\n电脑出的是:布\n对不起你输了!')
          }
        } else {
          alert('你出的是:石头\n电脑出的是:石头\n平局,再来一局!')
        }

        break;
      case 2:
        if (text < math) {
          alert('你出的是:剪刀\n电脑出的是:布\n恭喜你赢了本局!')
        } else if (text == math) {
          alert('你出的是:剪刀\n电脑出的是剪刀\n平局,再来一局!')
        } else {
          alert('你出的是:剪刀\n电脑出的是:石头\n对不起你输了!')
        }
        break;
      case 3:
        if (text >= math) {
          if (math == 1) {
            alert('你出的是:布\n电脑出的是:石头\n恭喜你赢了本局!')
          }
          else if (math == 2) {
            alert('你出的是:布\n电脑出的是:剪刀\n对不起你输了!')
          } else {
            alert('你出的是:布\n电脑出的是:布\n平局,再来一局!')
          }
        }
        break;
    }
   if(i>=9) {
    alert('正在退出!')
    break
   }
    console.log(text,'最后');
    if(text>3){
      if(text==4) {
        alert('正在退出!')
        break
      }else {
        alert('请输入正确的数字!')
      }
    }else if(typeof text =='string') {
      alert('请输入正确的数字!')
    }else if( text == null) {
      alert('正在退出!')
      break
    }
  }
}